import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import Row from "./elements/Row";
import {  getPostWidth , IMG_LOAD_AFTER_COMPLETE_LAYOUT, IS_SCREEN_SHOT} from '../../../utils'
import { getDataNextSteps } from '../../../actions/themeActions'
import { connect } from 'react-redux';


class CollageTheme extends PureComponent {
  allImageQueue = [];

  state = {
    cardWidth: null,
    largeDiv: null,
    smallDiv: null,
    allFirstImage: false
  }


  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.Personalization.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }

componentDidMount() {
  this.onCollageDataFormation(this.props)
  const {wall, postData,  appendData } = this.props;

    let fixedHeightScrollBar = document.getElementById("scrlBarComanW")

    
     if (appendData && wall.Personalization.autoScrollStatus) {
      window.addEventListener('scroll',(event) => { 
          let fixedHeightScrollBar = document.getElementById("scrlBarComanW")
          let documentHeight = document.body.scrollHeight;
          var getBottom = window.innerHeight + window.scrollY;

          if (fixedHeightScrollBar.scrollHeight-10 < getBottom ) {
            setTimeout(() => this.requestData(), 1000)
          }

      });
  }
}


  onCollageDataFormation = (props) => {
    const { wall, postData } = props;
    var widnowWidth= document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth:'';
    const ColumnCount = wall.ThemeRule.numberOfCoumn;
    const { cardWidth, largeDiv, smallDiv } = getPostWidth(widnowWidth, ColumnCount, wall.Personalization.commerceTheme);
    this.setState({ cardWidth: cardWidth, largeDiv: largeDiv, smallDiv: smallDiv })

  }

  handleAllImageError = (e) => {

    try {
      const src = e.target.getAttribute("data-item-id");
  
      // if (!this.allImageQueue.some(i => i.src.includes(src))) {
        this.allImageQueue.push({ src, e });
        if (!this.state.allFirstImage) this.processAllImageQueue();
        // if (!this.state.allFirstImage) IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => {});
      // }
    } catch (error) {
    }

  };

  processAllImageQueue = () => {
    if (this.allImageQueue.length) {
      this.setState({ allFirstImage: true })
      const { src, e } = this.allImageQueue[0];
      if (src) {
        IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, this.nextProcessAllImageQueue)
      }
    }
  };
  nextProcessAllImageQueue = () => {
    try {
      this.allImageQueue.shift();
      this.processAllImageQueue()
    } catch (error) {
    }
  }

 

  render() {
    const { data, cardWidth, largeDiv, smallDiv } = this.state;
    const { postData, completeDataObject, adjustWidth, wall, webFilters, languageSetting, newPostData } = this.props;
    return (
      <div className="tb_sc_post_container" id="ts_theme_container">
         {
           (newPostData && newPostData.length > 0) ? newPostData.map((dataArr, index) => {

            if (IS_SCREEN_SHOT && newPostData && ((newPostData.length - 1) == index)) {
              setTimeout(() => {
                  let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                  if (dataUpdateImage && dataUpdateImage.length > 0) {
                      dataUpdateImage.forEach((item) => {
                          item.setAttribute("src", item.getAttribute("data-src"))
                          item.setAttribute("data-is-optmized", "1")
                      })
                  }
              }, 1300)
          }
             
            return dataArr.length == wall.ThemeRule.numberOfCoumn ? <Row dataArr={dataArr}  key={`post_id_${index}`} handleAllImageError={this.handleAllImageError} indexNo={index} {...this.props} cardWidth={cardWidth} largeDiv={largeDiv} smallDiv={smallDiv} /> : ''
           }) : null
         }
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),

  }
}
export default connect(mapStateToProps,mapDispatchToProps)(CollageTheme);